/** @author 薛鑫欣
 * @description 提现
*/
<template>
  <div>
    <div class="div1">
     <span  class="tv">请设置提现金额</span>
     <div style="float: left;text-align:left;margin-left:12px">
      <el-input   class="input_money" v-model="inputMoney"
      placeholder="请输入提现金额"
      :change="inputMoneyLimitNumber()"
      :disabled="balance <=0 ? true:false"
      ></el-input><br>
      <span  class="balance">您的可提现余额为{{balance}}元</span>
      </div>
    </div>
    <!-- 设置账户 -->
    <div class="no_account" v-if="!isShowAccount">
      <span  style="color:#3399ff">请先设置您的提现账户</span>&nbsp;&nbsp;&nbsp;
      <button  style="color:#3399ff;background:none;border:none;font-size:16px" class="setting" @click="settingCard">设置</button>
    </div>
    <!-- 选择账户 -->
    <div class="have_account" v-else>
      <span  style="color:#333333">请选择账户</span>&nbsp;&nbsp;&nbsp;&nbsp;
      <button  style="color:#3399ff;font-size:14px;background:inherit;border:none" @click="settingCard">继续设置提现账户</button><br>
      <el-radio-group v-model="choose_account" style="width:25%;margin-top:20px" class="radio">
        <div>
        <el-radio
        v-for="(val, index) in accountList"
        :label="val.id"
        :key="val.id"
        >
        <span style="font-weight:800;font-size:16px">{{val.card_number}}</span>
        <!-- 滑动显示内容 -->
         <el-popover
          placement="top"
          width="200"
          trigger="hover"
          :content="val.fullNumber">
          <button slot="reference">查看</button>
         </el-popover>
        <img src="../../../assets/images/delete2.png" width="18px" height="18px" style="margin-left:30px" @click="deleteCard(index, val.id)">
        </el-radio>
        </div>
      </el-radio-group>
    </div>
    <div class="div3">
      <el-button  v-if="!isShowAccount||inputMoney<=0" style="background:#f2f2f2;width:71px;height:31px;color:#a1a1a1;font-size:15px;border-radius:5px;line-height:5px" disabled>提现</el-button>
      <el-button  v-else style="background:#fff;width:71px;height:31px;color:#000;font-size:15px;border-radius:5px;line-height:5px;border-color:#000" @click="clickWithdraw">提现</el-button>
    </div>
    <!-- dialog -->
    <el-dialog title="添加银行卡账户" :visible.sync="settingFormVisible" style="text-align:center;text-size:22px" width="500px">
      <div style="width:100%;height:41px;background:#f2f2f2;margin:0 auto;" >
       <span>
         1. 请仔细填写银行卡信息，由于填写错误导致的资金流失，概不负责<br>
         2. 只支持提现到借记卡， 不支持提现到信用卡
       </span>
      </div>
  <el-form v-model="form">
    <el-form-item label="请填写账户方机构名称" style="text-align:left">
      <el-input v-model="form.name"  size="mini" style="width:100%"></el-input>
    </el-form-item>
  <el-form-item label="账户类型" style="text-align:left">
    <el-radio-group v-model="form.type" style="width:100%">
    <el-radio :label="'persons'">个人银行卡（对私账户）</el-radio>
    <el-radio :label="'company'">公司账户（对公账户）</el-radio>
  </el-radio-group>
  </el-form-item>
    <el-form-item label="开户银行" style="text-align:left">
      <el-select v-model="form.bank_name" placeholder="请选择开户银行" style="width:100%" >
        <el-option
        v-for="item in bankList"
        :key="item.id"
        :label="item.name"
        :value="item.id"
        >
        </el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="银行卡号" style="text-align:left">
      <el-input v-model="form.bank_number"  size="mini" style="width:100%"   maxlength="22" :change="inputBankNumLimitNumber()" ></el-input>
    </el-form-item>
    <el-form-item label="开户名" style="text-align:left">
      <el-input v-model="form.bank_people"  size="mini" style="width:100%"></el-input>
    </el-form-item>
    <el-form-item label="开户地区" style="text-align:left" >
      <el-select v-model="form.bank_addr" placeholder="请选择开户地区" style="width:100%">
        <el-option
        v-for="item in addrList"
        :key="item.code"
        :label="item.name"
        :value="item.code"
        >
        </el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="开户支行完整名称" style="text-align:left">
      <el-input v-model="form.bank_allname"  size="mini" style="width:100%"></el-input>
    </el-form-item>
  </el-form>
  <div style="width:100%;height:21px;text-align:left;padding-left:20px;margin-top:6px;color:#333" >
       <span>
         请对照银行对账单填写完整支行名称，如“广东省深圳市福田区福民支行”
       </span>
      </div>
  <div slot="footer" class="dialog-footer" style="text-align:center">
    <el-button @click="cancel">取 消</el-button>
    <el-button type="primary" @click="confirm">确 定</el-button>
  </div>
</el-dialog>
  </div>
</template>
<script>
import { addWithdrawAccount,
         bankCardList,
         deleteBankCard,
         bankList,
         provinceList,
         withDraw
         } from '../../../service/AccountService'
import { getLoginInfo } from '../../../assets/common/utils.js'
export default {
  created () {
    // 面包屑
    this.$store.dispatch('commitChangeTopTitles', ['账户管理', '账户信息'])
  },
  data () {
    return {
      company_id: '',
      balance: '',
      inputMoney: '',
      card_number: '',
      isShowAccount: false,
      settingFormVisible: false,
      form: {
        name: '',
        type: 'persons',
        bank_name: '',
        bank_number: '',
        bank_people: '',
        bank_addr: '',
        bank_allname: ''
      },
      formLabelWidth: '120px',
      choose_account: '',
      accountList: [],
      // 银行
      bankList: [],
      addrList: [],
      fullNumber: '',
      bankNumber: ''
    }
  },
  methods: {
    // 输入提现金额限制
    inputMoneyLimitNumber () {
      this.inputMoney = this.inputMoney.replace(/[^\d]/g, '')
    },
    inputBankNumLimitNumber () {
      this.bankNumber = this.form.bank_number
      this.bankNumber = this.bankNumber.replace(/[^\d]/g, '')
      this.form.bank_number = this.bankNumber
    },
    // 点击设置银行卡
    settingCard () {
      this.settingFormVisible = true
      // 清空之前设置的银行卡信息
      this.form = {
        name: '',
        type: 'persons',
        bank_name: '',
        bank_number: '',
        bank_people: '',
        bank_addr: '',
        bank_allname: ''
      }
      // 获得银行卡列表
      bankList().then(res => { if (res.data) { this.bankList = res.data } })
      // 获得省份
      provinceList().then(res => { if (res.data) { this.addrList = res.data } })
    },
    // 设置银行卡信息
    confirm () {
      if (!this.form.name) {
        this.$message({type: 'warning', message: '请填写账户方机构名称'})
        return null
      }
      if (!this.form.type) {
        this.$message({type: 'warning', message: '请选择账户类型'})
        return null
      }
      if (!this.form.bank_name) {
        this.$message({type: 'warning', message: '请选择开户银行'})
        return null
      }
      if (!this.form.bank_number) {
        this.$message({type: 'warning', message: '请输入银行卡号'})
        return null
      }
      if (!this.form.bank_people) {
        this.$message({type: 'warning', message: '请输入开户名'})
        return null
      }
      if (!this.form.bank_addr) {
        this.$message({type: 'warning', message: '请选择开户地区'})
        return null
      }
      if (!this.form.bank_allname) {
        this.$message({type: 'warning', message: '请输入开户支行'})
        return null
      }
      let params = {}
      params['company_name'] = this.form.name
      params['account_type'] = this.form.type
      params['bank_id'] = parseInt(this.form.bank_name)
      params['card_number'] = this.form.bank_number
      params['card_name'] = this.form.bank_people
      params['province_code'] = this.form.bank_addr
      params['sub_branch_name'] = this.form.bank_allname
      params['user_type'] = 'homemaking'
      params['user_type_id'] = this.company_id
      // 添加银行卡
      addWithdrawAccount(params)
        .then(res => {
          if (res.status === 'success') {
            if (this.accountList.length > 0) {
              // 截取银行卡号
              this.substrCard()
              this.isShowAccount = true
            }
            this.settingFormVisible = false
            this.$message({type: 'success', message: '添加银行卡成功'})
            this.getCardList()
          } else {
            // this.$message({type: 'warning', message: res.msg})
          }
        }).catch(function (e) {
          this.$message({type: 'error', message: e})
        })
    },
    // 取消设置银行卡
    cancel () {
      this.settingFormVisible = false
    },
    // 删除银行卡
    deleteCard (index, id) {
      let params = {}
      params['bank_card_id'] = id
      params['company_id'] = this.company_id
      this.$confirm('是否删除该账户？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        center: true
      }).then(() => {
        deleteBankCard(params)
          .then(res => {
            this.accountList.splice(index, 1)
            this.$message({
              type: 'success',
              message: '删除成功!'
            })
            // 没有账户的情况
            if (this.accountList.length === 0) {
              this.isShowAccount = false
            }
          }).catch(function (e) {
            this.$message({
              type: 'error',
              message: e
            })
          })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    },
    // 提现
    clickWithdraw () {
      if (!this.choose_account) {
        this.$alert('请选择您要提现的账户！', '友情提示', {
          confirmButtonText: '确 定',
          callback: action => {}
        })
        return null
      }
      if (this.inputMoney > this.balance) {
        this.$alert('不能超过可提现的最大余额！', '友情提示', {
          confirmButtonText: '确 定',
          callback: action => {}
        })
        return null
      }
      let params = {}
      params['bank_card_id'] = parseInt(this.choose_account)
      params['company_id'] = this.company_id
      params['price'] = this.inputMoney
      // 提现
      withDraw(params)
        .then(res => {
          if (res.status === 'success') {
            this.$message({type: 'success', message: '提现成功'})
            this.$router.push('/accountInfo')
          } else {
            this.$message({type: 'warning', message: res.msg})
          }
        }).catch(function (e) {
          this.$message({type: 'error', message: e})
        })
    },
    // 截取银行卡号
    substrCard () {
      this.accountList.forEach((item, index) => {
        if (item.card_number) {
          if (item.card_number.length > 8) {
            item.fullNumber = item.card_number
            var start = item.card_number.substring(0, 4)
            var end = item.card_number.substring(item.card_number.length - 4, item.card_number.length)
            for (var i = 0; i < item.card_number.length - 8; i++) {
              start = start + '*'
            }
            start = start + end
            item.card_number = start
          }
        }
      })
    },
    // 获得我的银行卡列表
    getCardList () {
      var params = this.company_id
      bankCardList(params)
        .then(res => {
          if (res.data && res.data.length > 0) {
            this.isShowAccount = true
            this.accountList = res.data
            // 截取银行卡号
            this.substrCard()
          }
        })
    }
  },
  mounted () {
    // 获得公司id
    this.company_id = getLoginInfo('company_id')
    // 设置可提现余额
    this.balance = localStorage.getItem('balance')
    // 获得我的银行卡列表
    this.getCardList()
  }
}
</script>
<style scoped>
div >>>  .el-dialog__close {
    position: relative;
    bottom: 10px;
}
.el-form-item__content {
    line-height: 0px;
}
div >>> .el-input__inner {
  height:28px;
}
div >>> .div1 .el-input__inner {
  height:28px;
   background: inherit;
  border:none;
}
div >>> .el-radio{
  width: 100%;
  padding-bottom: 10px;
}
.radio {
  padding-top: 10px;
  width: 100%;
}
div >>>.el-dialog__body {
    padding: 0px;
    color: #606266;
    font-size: 14px;
    word-break: break-all;
}
div >>> .el-dialog__header {
  padding: 0px;
  padding-top: 5px;
  padding-bottom: 5px;
}
div >>>.el-form-item{
  margin: 0px;
  padding: 0px 20px;
}
div >>> .el-form-item__label {
  color: #333;
  font-weight: 700;
}
.div1{
  text-align: left;
  margin-top: 50px;
  padding: 20px;
  display: flex;
}
.tv {
  float: left;
  display: inline-block;
}
.input_money{
  display: inline-block;
  width: 249px;
  border: none;
  background: inherit;
  border-bottom: 1px solid #000;
  margin-bottom: 10px;
}
.balance {
  color: #999;
  font-size: 15px;
}
.no_account{
  text-align: left;
  display: flex;
  padding: 20px;
}
.have_account{
  text-align: left;
  padding: 20px;
}
.have_account button {
  border:none;
  background: inherit;
  font-size: 15px;
}
.div3{
  display: flex;
  padding-left: 20px;
}
.setting{
  text-decoration: underline;
}
</style>



